/*--ORIGINAL LESS---------------------------------------------------------------------------------------------------------------------------------------------*/
@import (less) 'color.less';

.btn {
    border-radius: 2px !important;
}

.button {
    font-weight: bold;
    font-size: 0.750em;
    padding-top: 1em;
    padding-bottom: 1em;
    line-height: 1;
    outline: none !important;
    text-transform: uppercase;
    height: 3.3334em;
    min-width: 6.875em;
    box-shadow: none !important;
}

.button.primary {
    color: @white;
    background-color: @green;
    border-color: @green;
    &:hover:not(.select) {
        background-color: @darkest_gray;
        color: @white;
        border-color: @darkest_gray;
    }
}

.button.default {
    color: @gray;
    background-color: @dark_white;
    border-color: @lightest_gray;
    &:hover:not(.select) {
        background-color: @darkest_gray;
        color: @white;
        border-color: @darkest_gray;
    }
}

.button.secondary{
  color: @gray;
    background-color: @white;
    border-color: @gray;
    &:hover:not(.select) {
        background-color: @darkest_gray;
        color: @white;
        border-color: @darkest_gray;
    }
}

.button.outline{
    color: @lightest_gray;
    background-color: Transparent;
    background-repeat:no-repeat;
    border-color: @lightest_gray;
    &:hover:not(.select) {
        background-color: @green;
        color: @lightest_gray;
        border-color: @green;
    }
}

.btn.primary[disabled]{
    &:hover {
        color: @white;
        background-color: @green !important;
        border-color: @green !important;
        cursor: not-allowed;
    }
}

.btn.default[disabled]{
    opacity:1 !important;
    background-color:#FFFFFF !important;
    color: @light_gray;
    &:hover {
        color: @light_gray;
        background-color: #FFFFFF !important;
        border-color: @lightest_gray !important;
        cursor: not-allowed;
    }
}

.btn.secondary[disabled]{
    &:hover {
        color: @gray;
        background-color: @white !important;
        border-color: @lightest_gray !important;
        cursor: not-allowed;
    }
}

.btn.outline[disabled]{
        color: @light_gray;
        background-color: Transparent !important;
        border-color: @light_gray !important;
        cursor: not-allowed;
    &:hover {
        color: @light_gray;
        background-color: Transparent !important;
        border-color: @light_gray !important;
        cursor: not-allowed;
    }
}







.button-icon {
    font-size: 2em !important;
    display: table-cell !important;
    top: -0.2125em;
    position: relative;
}

.button-label-icon {
    padding-left: 0.625em;
    padding-bottom: 0.8125em;
    display: table-cell !important;
    vertical-align: middle;
}

.button.selection-button {
    width: 29.6875em;
    height: 5.625em;
    background-color: @lightest_gray;
    border: none;
    border-radius: 1.5625em !important;
    -moz-box-shadow: none;
    -webkit-box-shadow: none;
    box-shadow: none;
    font-size: 1em;
    color: @green;
    text-align: left;
    &:hover {
        background-color: @green;
        color: @white;
    }
    .button-label-icon {
        padding-bottom: 0em;
        padding-left: 4.8em;
        text-transform: none;
        font-weight: normal;
    }
    .button-icon {
        top: 0.12em;
        left: 1em;
    }
}

.button.action {
    color: @darkest_gray;
    background-color: @lightest_gray;
    border-color: @lightest_gray;
    .button-icon {
        color: @green;
    }
    &:hover {
        background-color: @darkest_gray;
        color: @white;
        border: none;
        & .button-icon {
            color: @white;
        }
    }
}

.btn.action[disabled] {
    color: @gray;
    background-color: @dark_white !important;
    border-color: @lightest_gray !important;
    opacity: 0.5;
    &:hover {
        color: @gray;
        background-color: @dark_white !important;
        border: 1px solid @lightest_gray !important;
        cursor: default;
        .button-icon {
            color: @green;
        }
    }
    &.green {
        .button-icon {
            color: @green !important;
        }
    }
}


