        height: 3.334em;
        width: 18.334em;
        border: 1px dashed @black;
        border-radius: 1.5625em !important;
        background-color: @white;
        color: @black;
        &:hover {
            color: @white;
            background-color:@black;
            border: 1px dashed @white;
            .button-icon {
                color: @white;
            }
        }
        .button-icon {
            color: @black;
            font-size: 1.425em !important;
        }
    }

    .button.generative-3 {
        color: @black;
        background-color:@white;
        &:hover {
            .button-icon {
                color: @white;

                -moz-box-shadow: 0px 0px 2px @white;
                -webkit-box-shadow: 0px 0px 2px @white;
                box-shadow: 0px 0px 2px @white;
                background-color: @black;
            }
        }
        .button-icon {
            color: @black;

            padding: 1px 3px;
            display: inline-block;
            -moz-border-radius: 6.25em;
            -webkit-border-radius: 6.25em;
            border-radius: 6.25em;

            -moz-box-shadow: 0px 0px 2px @black;
            -webkit-box-shadow: 0px 0px 2px @black;
            box-shadow: 0px 0px 2px @black;
            &.ico-comuni-aggiungi:before {
                content: "\e01f";
                position: relative;
                top: 2px;
            }
        }
    }

    .allineamento-generative {
        display: inline-block !important;
        text-align: center;
    }

    .green-event {
        background-color: @green !important;
        color: @white !important;
    }

    .gray-event {
        background-color: @lightest_gray !important;
        color: @gray !important;
        border-color: @lightest_gray !important;
    }
}